import React from 'react'
import SVGWrapper from './svg-wrapper'

import SvgList from './svgs'
import TextIcon from './text-icon'
import css from './icons.module.css'

const icons = SvgList.reduce((icons, { iconType, name, svg, text, title }) => {
  icons[name] = function(props) {
    const child = svg || <TextIcon text={text} />

    return (
      <SVGWrapper key={name} title={title} iconType={iconType} {...props}>
        {child}
      </SVGWrapper>
    )
  }

  icons[name].title = title
  return icons
}, {})

